

body{

    background-color: white;
    margin: 6vmin;
    overflow: hidden;
}
.statement{
    color: black;
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 2.8vmax;
    line-height: 6vmin;
    position:relative;
    width:88vw;
    height: 60vh; 
 //  border: 2px solid green;
}
.statement:hover .en{
    -webkit-transition: 50s linear;
    -o-transition: 50s linear;
    transition: 50s linear;
    top: 100%;
    right:-10%
}
.statement:hover .fr{
    -webkit-transition: 50s linear;
    -o-transition: 50s linear;
    transition: 50s linear;
    top: -200%;
    right:-10%;
}

.en {
    position: absolute;
    top:0;
    right:0vw;
    width: 100%;
    height: 35vh;
    -webkit-transition: 10s linear;
    -o-transition: 10s linear;
    transition: 10s linear;
   // border: 2px solid red  ;
}
.fr {

    position: absolute;
    top:20vh;
    right:0vw;
    width: 100%;
    height: 35vh;
    -webkit-transition: 10s linear;
    -o-transition: 10s linear;
    transition: 10s linear;
    //border: 2px solid blue;
}
.en:hover .en{
        -webkit-transition: 30s linear;
    -o-transition: 30s linear;
    transition: 30s linear;
    top: -200%;
    right:10%;
}
.fr:hover .fr{
        -webkit-transition: 30s linear;
    -o-transition: 30s linear;
    transition: 30s linear;
    top: 200%;
    right:20%;
}
.vidDiv{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-line-pack: center;

            -ms-flex-align: center;

                align-items: center;
        position: absolute;
    height:100vh;
    width:95vw;
    margin:-3.8vmin;

}
.vid{
    width: 500px;
    height: 281px;
    border: 13px solid blue  ;
    z-index: 1;
}

@media screen and (max-width: 500px) {
    body {
        overflow: visible !important;
    }
    .vid{
     height: 50.25vw;
    }
}




